@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&family=Quicksand:wght@300;400&family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,500,300,700);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Merriweather&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800&display=swap);

*,
*::after,
*::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

:active,
:focus,
:hover {
  outline: 0 !important;
  outline-offset: 0;
}

a,
a:hover {
  text-decoration: none;
}

a:hover {
  color: #081158;
  color: var(--primary-color);
}

ul,
ol {
  margin: 0;
  padding: 0;
}

:root {
  --primary-color: #081158;
  --text-color: #fff;
  --primary-font: "Roboto", sans-serif;
  --secondary-font: "Quicksand", sans-serif;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Roboto", sans-serif;
  font-family: var(--primary-font);
  font-size: 62.5%;
  font-weight: 400;
  overflow-x: hidden;
}

::-webkit-scrollbar {
  width: 1rem;
}
::-webkit-scrollbar-track {
  background-color: #fff;
  background-color: var(--text-color);
}
::-webkit-scrollbar-thumb {
  background: #081158;
  background: var(--primary-color);
}

h1 {
  font-size: 3.2rem;
  font-weight: 900;
  /* margin-bottom: ; */
  color: #081158;
  color: var(--primary-color);
}
h2 {
  font-size: 2.5rem;
  font-weight: 700;
  /* margin-bottom: ; */
  color: #081158;
  color: var(--primary-color);
  font-family: "Quicksand", sans-serif;
  font-family: var(--secondary-font);
  text-transform: capitalize;
  line-height: 3rem;
}

h5 {
  font-size: 1.2rem;
  font-weight: 700;
  /* margin-bottom: ; */
  color: #081158;
  color: var(--primary-color);
  font-family: "Quicksand", sans-serif;
  font-family: var(--secondary-font);
  text-transform: capitalize;
  line-height: 1.7rem;
}
p {
  font-size: 1.2rem;
  font-weight: 400;
  /* margin-bottom: ; */
  color: #081158;
  color: var(--primary-color);
  font-family: "Quicksand", sans-serif;
  font-family: var(--secondary-font);
  text-transform: capitalize;
  line-height: 2rem;
  letter-spacing: 1px;
}

.main-btn {
  display: inline-block;
  padding: 0.5rem 1.5rem;
  line-height: 1.7rem;
  background-color: #081158;
  background-color: var(--primary-color);
  color: #fff;
  color: var(--text-color);
  font-size: 1rem;
  font-weight: 600;
  text-transform: uppercase;
  box-shadow: 0 2px 10px -1px rgb(0 0 0 / 19%);
  transition: all 0.4s ease-out 0s;
  border: 0.1px solid transparent;
  border-radius: 5px;
}
.dropdown ul li .nav-link {
  padding: 10px 0 !important;
}
.cart {
  display: inline-block;
  padding: 0.5rem 1.5rem;
  line-height: 1.7rem;
  color: #081158;
  color: var(--primary-color);
  font-size: 1rem;
  font-weight: 600;
}
.mcart {
  padding: 4.8px 0px 0 !important;
}
.mcart i {
  padding: 8px 18px;
}
.main-btn:hover {
  background-color: #fff;
  background-color: var(--text-color);
  color: #081158;
  color: var(--primary-color);
  border-color: #081158;
  border-color: var(--primary-color);
  transform: scaleX(1.1);
}
.nav {
  background: aliceblue;
}
.nav .container {
  max-width: 1220px;
}

/* nav style */
.shows {
  background: #fff !important;
}
.active1 {
  /* background: linear-gradient(
135deg, #428dce 0%,#6bb0db 50%,#59a7db 51%,#a6d1ea 100%); */
  background: #fff;
}
nav a img {
  height: 20px;
}
.navb {
  background: #fff;
  text-align: center;
}
.nav-wrap {
  position: fixed;
  width: 100%;
  left: 0;
  z-index: 1000;
  transition: all 0.3s ease-out;
}
.nav-wrap2 {
  position: inherit;
}

.nav-wrap .nav-item {
  padding: 0 1rem;
  transition: all 200ms linear;
  padding-top: 0.3rem;
}
.navbar-toggler:focus {
  outline: unset;
  border: unset;
  box-shadow: none;
}
.nav-link {
  font-size: 1rem;
  font-weight: 600;
  text-transform: capitalize;
  color: #081158;
  color: var(--primary-color);
  letter-spacing: 1px;
}

.nav-wrap .main-btn {
  box-shadow: none;
  padding-left: 18px;
}

/* for scroll */

.navbar-fixed-top.scrolled {
  box-shadow: 0 0.2rem 1rem 0 rgb(0, 0, 0, 0.09);
  background: #fff;
  background: var(--text-color);
  transition: background-color 200ms linear;
}
/* about */
/* text animation */
.text_1 {
  animation: text1;
}

.text_2 {
  animation: text2;
}

.text_1,
.text_2 {
  overflow: hidden;
  white-space: nowrap;
  display: inline-block;
  position: relative;
  animation-duration: 8s;
  animation-timing-function: steps(15, end);
  animation-iteration-count: infinite;
}
.stext_1 {
  animation: stext1;
}
.stext_2 {
  animation: stext2;
}
.stext_3 {
  animation: stext3;
}
.stext_1,
.stext_2,
.stext_3 {
  overflow: hidden;
  white-space: nowrap;
  display: inline-block;
  position: absolute;
  animation-duration: 15s;
  animation-timing-function: steps(10, end);
  animation-iteration-count: infinite;
  font-weight: 900;
}
.text_1::after,
.text_2::after,
.stext_1::after,
.stext_2::after,
.stext_3::after {
  content: "|";
  position: absolute;
  right: 0;
  animation: caret infinite;
  animation-duration: 1s;
  animation-timing-function: steps(1, end);
}

@keyframes text2 {
  0%,
  50%,
  100% {
    width: 0;
  }

  60%,
  90% {
    width: 8em;
  }
}

@keyframes text1 {
  0%,
  40%,
  70%,
  100% {
    width: 0;
  }
  10%,
  30% {
    width: 7em;
  }
}
@keyframes stext3 {
  0%,
  40%,
  70%,
  100% {
    width: 0;
  }

  80%,
  95% {
    width: 8em;
  }
}
@keyframes stext2 {
  0%,
  40%,
  70%,
  100% {
    width: 0;
  }

  50%,
  65% {
    width: 4em;
  }
}

@keyframes stext1 {
  0%,
  40%,
  70%,
  100% {
    width: 0;
  }
  10%,
  30% {
    width: 3em;
  }
}

@keyframes caret {
  0%,
  100% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}
