/* client */

#client {
  position: relative;
  padding: 25px 0;

  background: #081158;

  background: var(--primary-color);
  overflow-x: clip;
}
#client .cli {
  color: #fff !important;
  color: var(--text-color) !important;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#client .cli h2,
#client .cli p {
  color: #fff !important;
  color: var(--text-color) !important;
  z-index: 100;
}
#client .blue-line {
  position: absolute;
  top: -15px;
  width: 100%;
  height: 7vh;
  background: #081158;
  background: var(--primary-color);
  transform: rotate(-1deg);
}

#client .blue-line2 {
  position: absolute;
  bottom: -15px;
  width: 101%;
  height: 7vh;
  background: #081158;
  background: var(--primary-color);
  transform: rotate(-1deg);
}

