/* //container-fluid style */
.Tmain {
  background-color: #ededed;
  height: 100%;
  min-height: 70vh;
  padding: 1rem;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  color: hsl(198, 1%, 29%);
  font-family: "Architects Daughter", cursive;
  text-align: center;
  font-size: 130%;
  padding-top: 150px;
  padding-bottom: 70px;
}

.Tcontainer {
  width: 100%;
  height: auto;
  /* min-height: 500px; */
  max-width: 500px;
  min-width: 250px;
  background: #f1f5f8;
  background-image: radial-gradient(#bfc0c1 7.2%, transparent 0);
  background-size: 25px 25px;
  border-radius: 20px;
  box-shadow: 4px 3px 7px 2px #00000040;
  padding: 1rem;
  box-sizing: border-box;
}

/* //Heading style */
.Theading {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1rem;
}
.Theading h1 {
  transform: rotate(2deg);
  padding: 0.2rem 1.2rem;
  border-radius: 20% 5% 20% 5%/5% 20% 25% 20%;
  background-color: #fdcb6e;
  font-size: 1.5rem;
}

/* //Form style */
.Tform input {
  box-sizing: border-box;
  background-color: transparent;
  padding: 0.7rem;
  border-bottom-right-radius: 15px 3px;
  border-bottom-left-radius: 3px 15px;
  border: solid 3px transparent;
  border-bottom: dashed 3px #fdcb6e;
  font-family: "Architects Daughter", cursive;
  font-size: 1rem;
  color: hsla(260, 2%, 25%, 0.7);
  width: 70%;
  margin-bottom: 20px;
}

.Tbtn {
  padding: 0;
  border: none;
  font-family: "Architects Daughter", cursive;
  text-decoration: none;
  padding-bottom: 3px;
  border-radius: 5px;
  /* background-color: #ffeaa7; */
}

.Tbtn span {
  background: #fdc761;
  display: block;
  padding: 0.5rem 1rem;
  border-radius: 5px;
}
.Tbtn span:hover {
  background: #f6b336;
}

/* List Styling */
.Tlist ul {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
  background-color: transparent;
  padding: 0.7rem 2.5rem;
  border-bottom-right-radius: 15px 3px;
  border-bottom-left-radius: 3px 15px;
  border: solid 3px transparent;
  font-family: "Architects Daughter", cursive;
  font-size: 0.9rem;
  color: hsla(260, 2%, 25%, 0.7);
  width: 100%;
}
.Tlist ul li {
  text-align: left;
  position: relative;
  padding: 0.5rem 0.3rem;
}
.Tlist .liItem {
  background-color: #fff;
  /* background-color: #d5d5d5c7; */
  display: flex;
  justify-content: space-between;
  padding: 02px 10px 2px 30px;
  border-radius: 10px;
  margin: 5px 0;
}

/* //check Button style */
.TbtnCheck {
  padding: 0;
  border: none;
  font-family: "Roboto", sans-serif;
  text-decoration: none;
  padding-bottom: 3px;
  border-radius: 5px;
}
.TbtnCheck span {
