* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body,
input,
textarea {
  font-family: "Poppins", sans-serif;
}

.contact_container {
  position: relative;
  width: 100%;
  min-height: 100vh;
  padding: 2rem;
  background-color: #fafafa;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.contact_container .form {
  width: 100%;
  max-width: 950px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 0 20px 1px rgba(0, 0, 0, 0.1);
  z-index: 1000;
  overflow: hidden;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.contact_container .contact-form {
  background-color: #1A8BBC;
  position: relative;
}

.contact_container .circle {
  border-radius: 50%;
  background: linear-gradient(135deg, transparent 20%, #99d6f0);
  position: absolute;
}

.contact_container .circle.one {
  width: 130px;
  height: 130px;
  top: 130px;
  right: -40px;
}

.contact_container .circle.two {
  width: 80px;
  height: 80px;
  top: 10px;
  right: 30px;
}

.contact_container .contact-form:before {
  content: "";
  position: absolute;
  width: 26px;
  height: 26px;
  background-color: #1A8BBC;
  transform: rotate(45deg);
  top: 50px;
  left: -13px;
}

.contact_container form {
  padding: 2.3rem 2.2rem;
  z-index: 10;
  overflow: hidden;
  position: relative;
}

.contact_container .title {
  color: #fff;
  font-weight: 500;
  font-size: 1.5rem;
  line-height: 1;
  margin-bottom: 0.7rem;
}

.contact_container .input-container {
  position: relative;
  margin: 1rem 0;
}

.contact_container .input {
  width: 100%;
  outline: none;
  border: 2px solid #fafafa;
  background: none;
  padding: 0.6rem 1.2rem;
  color: #fff;
  font-weight: 500;
  font-size: 0.95rem;
  letter-spacing: 0.5px;
  border-radius: 25px;
  transition: 0.3s;
}

.contact_container textarea.input {
  padding: 0.8rem 1.2rem;
  min-height: 150px;
  border-radius: 22px;
  resize: none;
  overflow-y: auto;
}

.contact_container .input-container label {
  position: absolute;
  top: 50%;
  left: 15px;
  transform: translateY(-50%);
  padding: 0 0.4rem;
  color: #fafafa;
  font-size: 0.9rem;
  font-weight: 400;
  pointer-events: none;
  z-index: 1000;
  transition: 0.5s;
}

.contact_container .input-container.textarea label {
  top: 1rem;
  transform: translateY(0);
}

.contact_container .btn {
  padding: 0.6rem 1.3rem;
  background-color: #fff;
  border: 2px solid #fafafa;
  font-size: 0.95rem;
  color: #1A8BBC;
  line-height: 1;
  border-radius: 25px;
  outline: none;
  cursor: pointer;
  transition: 0.3s;
  margin: 0;
}

.contact_container .btn:hover {
  background-color: transparent;
  color: #fff;
}

.contact_container .input-container span {
  position: absolute;
  top: 0;
  left: 25px;
  transform: translateY(-50%);
  font-size: 0.8rem;
  padding: 0 0.4rem;
  color: transparent;
  pointer-events: none;
  z-index: 500;
}

.contact_container .input-container span:before,
.contact_container .input-container span:after {
  content: "";
  position: absolute;
  width: 10%;
  opacity: 0;
  transition: 0.3s;
  height: 5px;
  background-color: #1A8BBC;
  top: 50%;
  transform: translateY(-50%);
}

.contact_container .input-container span:before {
  left: 50%;
}

.contact_container .input-container span:after {
  right: 50%;
}

.contact_container .input-container.focus label {
  top: 0;
  transform: translateY(-50%);
  left: 25px;
  font-size: 0.8rem;
}

.contact_container .input-container.focus span:before,
.contact_container .input-container.focus span:after {
  width: 50%;
  opacity: 1;
}

.contact_container .contact-info {
  padding: 2.3rem 2.2rem;
  position: relative;
}

.contact_container .contact-info .title {
  color: #1A8BBC;
}

.contact_container .text {
  color: #333;
  margin: 1.5rem 0 2rem 0;
}

.contact_container .information {
  display: flex;
  color: #555;
  margin: 0.7rem 0;
  align-items: center;
  font-size: 0.95rem;
}

.contact_container .icon {
  width: 28px;
  margin-right: 0.7rem;
margin-bottom: 11px;
}
.contact_container .information .email{
    text-transform: lowercase;
}
.social-media {
  padding: 2rem 0 0 0;
}

.contact_container .social-media p {
  color: #333;
}

.contact_container .social-icons {
  display: flex;
  margin-top: 0.5rem;
}

.contact_container .social-icons a {
  width: 35px;
  height: 35px;
  border-radius: 5px;
  background: linear-gradient(45deg, #1A8BBC, #1A8BBC);
  color: #fff;
  text-align: center;
  line-height: 35px;
  margin-right: 0.5rem;
  transition: 0.3s;
}

.contact_container .social-icons a:hover {
  transform: scale(1.05);
}

.contact_container .contact-info:before {
  content: "";
  position: absolute;
  width: 123px;
  height: 100px;
  border: 22px solid #1A8BBC;
  border-radius: 50%;
  bottom: -63px;
  right: 50px;
  opacity: 0.3;
}

.contact_container .big-circle {
  position: absolute;
  width: 500px;
  height: 500px;
  border-radius: 50%;
  background: linear-gradient(to bottom,#cfe9f5, #0ca9ec);
  bottom: 52%;
  right: 52%;
  transform: translate(-40%, 38%);
}

.contact_container .big-circle:after {
  content: "";
  position: absolute;
  width: 360px;
  height: 360px;
  background-color: #fafafa;
  border-radius: 50%;
  top: calc(50% - 180px);
  left: calc(50% - 180px);
}

.contact_container .square {
  position: absolute;
  height: 400px;
  top: 50%;
  left: 50%;
  transform: translate(181%, 11%);
  opacity: 0.2;
}

@media (max-width: 850px) {
  .contact_container .form {
    grid-template-columns: 1fr;
  }

  .contact_container .contact-info:before {
    bottom: auto;
    bottom: initial;
    top: -75px;
    right: 65px;
    transform: scale(0.95);
  }

  .contact_container .contact-form:before {
    top: -13px;
    left: auto;
    left: initial;
    right: 70px;
  }

  .contact_container .square {
    transform: translate(140%, 43%);
    height: 350px;
  }

  .contact_container .big-circle {
    bottom: 75%;
    transform: scale(0.9) translate(-40%, 30%);
    right: 50%;
  }

  .contact_container .text {
    margin: 1rem 0 1.5rem 0;
  }

  .contact_container .social-media {
    padding: 1.5rem 0 0 0;
  }
}

@media (max-width: 480px) {
  .contact_container .contact_container {
    padding: 1.5rem;
  }

  .contact_container .contact-info:before {
    display: none;
  }

  .contact_container .square,
  .contact_container .big-circle {
    display: none;
  }

  .contact_container form,
  .contact_container .contact-info {
    padding: 1.7rem 1.6rem;
  }

  .contact_container .text,
  .contact_container .information,
  .contact_container .social-media p {
    font-size: 0.8rem;
  }

  .contact_container .title {
    font-size: 1.15rem;
  }

  .contact_container .social-icons a {
    width: 30px;
    height: 30px;
    line-height: 30px;
  }

  .contact_container .icon {
    width: 23px;
  }

  .contact_container .input {
    padding: 0.45rem 1.2rem;
  }

  .contact_container .btn {
    padding: 0.45rem 1.2rem;
  }
}

